*{
    margin: 0; 
    padding: 0;
    box-sizing: border-box;
  }
 
html, body {
    width: 100%;
    height: 100%;
}

header, nav, footer{
    width: 100%;
    height: 10%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;  }

h1, h2{
    width: 100%;
    text-align: center;
}

nav{
    margin: 10% 0 0 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center; 
    
    #languageBtn, #races, #weapon, #magic, #tales{
        width: 50px;
        height: 50px;
        border-radius: 10px;
        border: 1px solid #0f52ba;
        outline: none;
        font-size: 1.6em;
        font-weight: bold;
        background-color: #0f52ba;
        color: #fff;
    }

    #races, #weapon, #magic{
        font-size: 1em;
    }

    #races{
        background-color: #009900;
        border: 1px solid #009900;
    }

    #races:active{
        background-color: #00e600;
        border: 4px solid #009900;
        color: #000;
    }

    #weapon{
        background-color: #cc3300;
        border: 1px solid#cc3300;
    }

    #weapon:active{
        background-color: #ff6633;
        border: 4px solid #cc3300;
        color: #000;
    }

    #magic{
        background-color: #cc00cc;
        border: 1px solid #cc00cc;
        font-size: 0.9em;
    }

    #magic:active{
        background-color: #ff66ff;
        border: 4px solid #cc00cc;
        color: #000;
    }

    #tales{
        background-color: #595959;
        border: 1px solid #595959;
        font-size: 0.7em;
    }

    #tales:active{
        background-color: #a6a6a6;
        border: 4px solid #595959;
        color: #000;
    }

    #languageBtn:active{
        background-color: #72a5f4;
        border: 4px solid #0f52ba;
        color: #000;
    }


}

#mainContainer{
    width: 90%;
    height: 80%;
    margin: 0 auto;
    overflow: auto;  
}